
.Home{
    margin-left: 0.5%;
    margin-right: 0.5%;
    margin-top: 0.5%;
    }

.player{
    background: -webkit-gradient(linear, right top, left bottom, from(#e0e0e0), to(#ffcc99));
    background: -webkit-linear-gradient(top right, #e0e0e0 0%, #ffcc99 100%);
    background: -o-linear-gradient(top right, #e0e0e0 0%, #ffcc99 100%);
    background: linear-gradient(to bottom left, #e0e0e0 0%, #ffcc99 100%);
    -webkit-box-shadow: 0 8px 8px 0 rgba(0,0,0,0.6);
            box-shadow: 0 8px 8px 0 rgba(0,0,0,0.6);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    padding: 14px 25px;
    
}

.player_info{
    background: -webkit-gradient(linear, right top, left bottom, from(#e0e0e0), to(#ffcc99));
    background: -webkit-linear-gradient(top right, #e0e0e0 0%, #ffcc99 100%);
    background: -o-linear-gradient(top right, #e0e0e0 0%, #ffcc99 100%);
    background: linear-gradient(to bottom left, #e0e0e0 0%, #ffcc99 100%);
    -webkit-box-shadow: 0 8px 8px 0 rgba(0,0,0,0.6);
            box-shadow: 0 8px 8px 0 rgba(0,0,0,0.6);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    padding: 14px 14px;
    
}
.player:hover{

    -webkit-box-shadow: 0 8px 16px 0 rgba(255,255,255,0.3);

            box-shadow: 0 8px 16px 0 rgba(255,255,255,0.3);
}
